<template>
  <div style="font-size: 24px; font-weight: bold;"></div>
  <a-divider></a-divider>
  <div class="subscribe-add" >
    <div style="text-align: left; ">
      <a-form
        labelAlign="right"
        :labelWrap="true"
        :model="item"
        size="small"
        @finish="editItem"
        :labelCol="{ span: 3 }"
        :wrapperCol="{ span: 21 }"
        autocomplete="off"
        :class="`container-form-${ isMobile() ? 'mobile' : 'pc' }`">
        <a-form-item
          label=""
          name="doubanId"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-select size="small" v-model:value="item.doubanId">
            <a-select-option v-for="subscribe of subscribes" v-model:value="subscribe.id" :key="subscribe.id">{{ subscribe.alias }}</a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          label=""
          name="name"
          extra=", ,  Vertex "
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="item.name"/>
        </a-form-item>
        <a-form-item
          label=""
          name="tag"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-select v-if="item.doubanId" size="small" v-model:value="item.tag">
            <a-select-option v-for="category of subscribes.filter(i => i.id === item.doubanId)[0].categories" v-model:value="category.doubanTag" :key="category.doubanTag">{{ category.doubanTag }}</a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          label=""
          name="year"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="item.year"/>
        </a-form-item>
        <a-form-item
          label=""
          name="releaseAt"
          extra=" 2022-06-03, , "
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="item.releaseAt"/>
        </a-form-item>
        <a-form-item
          label=""
          name="episodes"
          extra="">
          <a-input size="small" v-model:value="item.episodes"/>
        </a-form-item>
        <a-form-item
          label=""
          name="episodeNow">
          <a-input size="small" v-model:value="item.episodeNow"/>
        </a-form-item>
        <a-form-item
          label=""
          name="downloaded"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-checkbox v-model:checked="item.downloaded"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=""
          name="searchKey"
          extra=", ">
          <a-input size="small" v-model:value="item.searchKey"/>
        </a-form-item>
        <a-form-item
          label=""
          name="acceptKeys"
          extra=" , ,   ">
          <a-input size="small" v-model:value="item.acceptKeys"/>
        </a-form-item>
        <a-form-item
          label=""
          name="rejectKeys"
          extra=" , ,   ">
          <a-input size="small" v-model:value="item.rejectKeys"/>
        </a-form-item>
        <a-form-item
          label=""
          name="fixedSeason"
          extra=", ,  ,  0">
          <a-input size="small" v-model:value="item.fixedSeason"/>
        </a-form-item>
        <a-form-item
          label=""
          name="episodeOffset"
          extra=",  -2, -1, 1, 2 , , ">
          <a-input size="small" v-model:value="item.episodeOffset"/>
        </a-form-item>
        <a-form-item
          label=""
          name="restrictYear"
          extra=", ">
          <a-checkbox v-model:checked="item.restrictYear"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=""
          name="rejectCompleteTorrent"
          extra=", , ">
          <a-checkbox v-model:checked="item.rejectCompleteTorrent"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=""
          name="ignoreEpisodes"
          extra=", ">
          <a-checkbox v-model:checked="item.ignoreEpisodes"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=""
          name="cancelLink"
          extra=", ">
          <a-checkbox v-model:checked="item.cancelLink"></a-checkbox>
        </a-form-item>
        <a-form-item
          :wrapperCol="isMobile() ? { span:24 } : { span: 21, offset: 3 }">
          <a-button type="primary" html-type="submit" style="margin-top: 24px; margin-bottom: 48px;"></a-button>
        </a-form-item>
      </a-form>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      item: {
        name: '',
        tag: '',
        year: '',
        releaseAt: '',
        episodes: '',
        episodeNow: '',
        downloaded: false,
        searchKey: '',
        rejectKeys: '',
        fixedSeason: '',
        episodeOffset: '',
        restrictYear: false,
        rejectCompleteTorrent: false,
        ignoreEpisodes: false,
        cancelLink: false
      },
      subscribes: []
    };
  },
  methods: {
    isMobile () {
      if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
        return true;
      } else {
        return false;
      }
    },
    async editItem () {
      try {
        const res = await this.$api().subscribe.editItem(this.item);
        await this.$message().success(res.message);
        this.$goto('/subscribe/list', this.$router);
      } catch (e) {
        await this.$message().error(e.message);
      }
    },
    async listSubscribe () {
      try {
        const res = await this.$api().subscribe.list();
        this.subscribes = res.data;
      } catch (e) {
        this.$message().error(e.message);
      }
    }
  },
  async mounted () {
    await this.listSubscribe();
  }
};
</script>
<style scoped>
.subscribe-add {
  height: calc(100% - 92px);
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  text-align: center;
}

</style>
