<template>
  <div style="font-size: 24px; font-weight: bold;"></div>
  <a-divider></a-divider>
  <div class="select-rule">
    <a-table
      :style="`font-size: ${isMobile() ? '12px': '14px'};`"
      :columns="columns"
      size="small"
      :data-source="selectRuleList"
      :pagination="false"
      :scroll="{ x: 640 }"
    >
      <template #title>
        <span style="font-size: 16px; font-weight: bold;"></span>
      </template>
      <template #bodyCell="{ column, record }">
        <template v-if="column.dataIndex === 'sortType'">
          {{ (conditionKeys.filter(item => item.key === record.sortBy)[0] || {}).name }}
          {{ record.sortType ? record.sortType === 'asc' ? '/ ' : '/ ' : '' }}
        </template>
        <template v-if="column.title === ''">
          <span>
            <a @click="modifyClick(record)"></a>
            <a-divider type="vertical" />
            <a-popover title="?" trigger="click" :overlayStyle="{ width: '84px', overflow: 'hidden' }">
              <template #content>
                <a-button type="primary" danger @click="deleteSelectRule(record)" size="small"></a-button>
              </template>
              <a style="color: red"></a>
            </a-popover>
          </span>
        </template>
      </template>
    </a-table>
    <a-divider></a-divider>
    <div style="font-size: 16px; font-weight: bold; padding-left: 8px;"> | </div>
    <div style="text-align: left; ">
      <a-form
        labelAlign="right"
        :labelWrap="true"
        :model="selectRule"
        size="small"
        @finish="modifySelectRule"
        :labelCol="{ span: 3 }"
        :wrapperCol="{ span: 21 }"
        autocomplete="off"
        :class="`container-form-${ isMobile() ? 'mobile' : 'pc' }`">
        <a-form-item
          label=""
          name="alias"
          extra=""
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="selectRule.alias"/>
        </a-form-item>
        <a-form-item
          label=""
          name="priority"
          extra=",  0"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="selectRule.priority"/>
        </a-form-item>
        <a-form-item
          label=""
          name="sortBy"
          extra=", , , ">
          <a-select size="small" v-model:value="selectRule.sortBy"  >
            <a-select-option v-for="conditionKey of conditionKeys" :key="conditionKey.key" :value="conditionKey.key">{{ conditionKey.name }}</a-select-option>
          </a-select>
          <a-form-item-rest>
            <a-select size="small" v-model:value="selectRule.sortType"  >
              <a-select-option v-for="item of [{name: '', key: 'asc'}, {name: '', key: 'desc'}]" :key="item.key" :value="item.key">{{ item.name }}</a-select-option>
            </a-select>
          </a-form-item-rest>
        </a-form-item>
        <a-form-item
          label=""
          name="type"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-select size="small" v-model:value="selectRule.type"  >
            <a-select-option value="normal"></a-select-option>
            <a-select-option value="javascript">JavaScript</a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          label=""
          v-if="selectRule.type === 'normal'"
          name="conditions"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-form-item-rest>
            <a-table
              :style="`font-size: ${isMobile() ? '12px': '14px'};`"
              :columns="conditionColumns"
              size="small"
              :data-source="selectRule.conditions"
              :pagination="false"
              :scroll="{ x: 540 }"
            >
              <template #bodyCell="{ column, record }">
                <template v-if="column.dataIndex === 'key'">
                  <a-select size="small" v-model:value="record.key"  >
                    <a-select-option v-for="conditionKey of conditionKeys" :key="conditionKey.key" :value="conditionKey.key">{{ conditionKey.name }}</a-select-option>
                  </a-select>
                </template>
                <template v-if="column.dataIndex === 'compareType'">
                  <a-select size="small" v-model:value="record.compareType"  >
                    <a-select-option value="equals"></a-select-option>
                    <a-select-option value="bigger"></a-select-option>
                    <a-select-option value="smaller"></a-select-option>
                    <a-select-option value="contain"></a-select-option>
                    <a-select-option value="includeIn"></a-select-option>
                    <a-select-option value="notContain"></a-select-option>
                    <a-select-option value="notIncludeIn"></a-select-option>
                    <a-select-option value="regExp"></a-select-option>
                    <a-select-option value="notRegExp"></a-select-option>
                  </a-select>
                </template>
                <template v-if="column.dataIndex === 'value'">
                  <a-input size="small" v-model:value="record.value"/>
                </template>
                <template v-if="column.dataIndex === 'option'">
                  <a style="color: red" @click="selectRule.conditions = selectRule.conditions.filter(item => item !== record)"></a>
                </template>
              </template>
            </a-table>
            <a-button
              type="primary"
              @click="selectRule.conditions.push({ ...condition })"
              size="small">
              
            </a-button>
          </a-form-item-rest>
        </a-form-item>
        <a-form-item
          v-if="selectRule.type === 'javascript'"
          name="code"
          :rules="[{ required: true, message: '${label}! ' }]"
          label="">
          <a-textarea  v-model:value="selectRule.code" type="textarea" :rows="10"></a-textarea >
        </a-form-item>
        <a-form-item
          :wrapperCol="isMobile() ? { span:24 } : { span: 21, offset: 3 }">
          <a-button type="primary" html-type="submit" style="margin-top: 24px; margin-bottom: 48px;"> | </a-button>
          <a-button style="margin-left: 12px; margin-top: 24px; margin-bottom: 48px;"></a-button>
        </a-form-item>
      </a-form>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    const columns = [
      {
        title: 'ID',
        dataIndex: 'id',
        width: 18,
        fixed: true
      }, {
        title: '',
        dataIndex: 'alias',
        sorter: {
          compare: (a, b) => a.alias.localeCompare(b.alias),
          multiple: 1
        },
        width: 30
      }, {
        title: '',
        dataIndex: 'priority',
        width: 20,
        sorter: {
          compare: (a, b) => +a.priority - +b.priority,
          multiple: 2
        },
        defaultSortOrder: 'descend'
      }, {
        title: '',
        dataIndex: 'sortType',
        width: 30
      }, {
        title: '',
        width: 20
      }
    ];
    const conditionColumns = [
      {
        title: '',
        dataIndex: 'key',
        width: 18
      }, {
        title: '',
        dataIndex: 'compareType',
        width: 18
      }, {
        title: '',
        dataIndex: 'value',
        width: 90
      }, {
        title: '',
        dataIndex: 'option',
        width: 30
      }
    ];
    return {
      columns,
      conditionColumns,
      conditionKeys: [{
        name: '',
        key: 'sitePriority'
      }, {
        name: '',
        key: 'title'
      }, {
        name: '',
        key: 'subtitle'
      }, {
        name: '',
        key: 'size'
      }, {
        name: '',
        key: 'language'
      }, {
        name: '',
        key: 'area'
      }, {
        name: '',
        key: 'category'
      }, {
        name: '',
        key: 'seeders'
      }, {
        name: '',
        key: 'leechers'
      }, {
        name: '',
        key: 'snatches'
      }, {
        name: '',
        key: 'time'
      }, {
        name: '',
        key: 'tags'
      }],
      condition: {
        key: '',
        compareType: '',
        value: ''
      },
      selectRule: {},
      defaultSelectRule: {
        conditions: [{
          key: '',
          compareType: '',
          value: ''
        }],
        code: '(torrent) => {\n' +
              '  return false;\n' +
              '}'
      },
      loading: true,
      selectRuleList: []
    };
  },
  methods: {
    isMobile () {
      if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
        return true;
      } else {
        return false;
      }
    },
    async listSelectRule () {
      try {
        const res = await this.$api().selectRule.list();
        this.selectRuleList = res.data;
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    async modifySelectRule () {
      try {
        await this.$api().selectRule.modify({ ...this.selectRule });
        this.$message().success((this.selectRule.id ? '' : '') + ', ...');
        setTimeout(() => this.listSelectRule(), 1000);
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    modifyClick (row) {
      this.selectRule = { ...row };
    },
    async deleteSelectRule (row) {
      if (row.used) {
        this.$message().error(', ');
        return;
      }
      try {
        await this.$api().selectRule.delete(row.id);
        this.$message().success(', ...');
        await this.listSelectRule();
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    clearSelectRule () {
      this.selectRule = { ...this.SelectRule, conditions: [{ ...this.condition }] };
    }
  },
  async mounted () {
    this.clearSelectRule();
    await this.listSelectRule();
  }
};
</script>
<style scoped>
.select-rule {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}
</style>
