<template>
  <div style="font-size: 24px; font-weight: bold;"></div>
  <a-divider></a-divider>
  <div class="log">
    <div style="text-align: left; margin: 24px">
      <a-form
        labelAlign="right"
        :labelWrap="true"
        size="small"
        :labelCol="{ span: 3 }"
        :wrapperCol="{ span: 21 }"
        autocomplete="off"
        :class="`container-form-${ isMobile() ? 'mobile' : 'pc' }`">
        <a-form-item
          label=""
          name="type">
          <a-select size="small" v-model:value="type" @change="getLog">
            <a-select-option value="info"></a-select-option>
            <a-select-option value="binge"></a-select-option>
            <a-select-option value="binge-debug"></a-select-option>
            <a-select-option value="advanced"></a-select-option>
            <a-select-option value="advanced-debug"></a-select-option>
            <a-select-option value="watch"></a-select-option>
            <a-select-option value="watch-debug"></a-select-option>
            <a-select-option value="sc"></a-select-option>
            <a-select-option value="sc-debug"></a-select-option>
            <a-select-option value="error"></a-select-option>
            <a-select-option value="debug"></a-select-option>
            <a-select-option value="access"></a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          :wrapperCol="isMobile() ? { span:24 } : { span: 21, offset: 3 }">
          <a-button size="small" type="primary" @click="getLog"></a-button>
        </a-form-item>
        <a-form-item
          :wrapperCol="isMobile() ? { span:24 } : { span: 21, offset: 3 }">
          <p style="color: red; font-weight: bold;">: {{version.head}}/{{version.updateTime}} , </p>
          <p style="color: red; font-weight: bold;"> message / code / status </p>
          <p style="color: red; font-weight: bold;">, </p>
        </a-form-item>
        <a-form-item
          label="">
          <a-textarea v-model:value="log" type="textarea" autoSize></a-textarea>
        </a-form-item>
      </a-form>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      type: 'error',
      log: '',
      version: {}
    };
  },
  methods: {
    isMobile () {
      if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
        return true;
      } else {
        return false;
      }
    },
    async getLog () {
      try {
        const res = await this.$api().log.get(this.type);
        this.log = res.data;
        this.log = res ? '[202' + res.data.split('[202').reverse().join('[202') : '';
        this.log = this.log.replace(new RegExp(`\\[${this.$moment().format('YYYY')}-`, 'g'), '[').replace(/\[[^\d]*? console\] \d*/g, '').replace(/\[202/g, '');
      } catch (e) {
        await this.$message().error(e.message);
      }
    }
  },
  async mounted () {
    this.version = process.env.version;
    this.getLog();
  }
};
</script>
<style scoped>
.log {
  width: 100%;
  /*max-width: 1440px;*/
  margin: 0 auto;
}
</style>
