<template>
  <div style="font-size: 24px; font-weight: bold;"></div>
  <a-divider></a-divider>
  <div class="link-rule">
    <a-table
      :style="`font-size: ${isMobile() ? '12px': '14px'};`"
      :columns="columns"
      size="small"
      :data-source="linkRules"
      :pagination="false"
      :scroll="{ x: 640 }"
    >
      <template #title>
        <span style="font-size: 16px; font-weight: bold;"></span>
      </template>
      <template #bodyCell="{ column, record }">
        <template v-if="column.dataIndex === 'linkMode'">
          {{ record.hardlink ? '' : '' }}
        </template>
        <template v-if="column.dataIndex === 'server'">
          {{ record.server ? this.serverList.filter(item => item.id === record.server)[0].alias : '' }}
        </template>
        <template v-if="column.title === ''">
          <span>
            <a @click="modifyClick(record)"></a>
            <a-divider type="vertical" />
            <a-popover title="?" trigger="click" :overlayStyle="{ width: '84px', overflow: 'hidden' }">
              <template #content>
                <a-button type="primary" danger @click="deleteLinkRule(record)" size="small"></a-button>
              </template>
              <a style="color: red"></a>
            </a-popover>
          </span>
        </template>
      </template>
    </a-table>
    <a-divider></a-divider>
    <div style="font-size: 16px; font-weight: bold; padding-left: 8px;"> | </div>
    <div style="text-align: left; ">
      <a-form
        labelAlign="right"
        :labelWrap="true"
        :model="linkRule"
        size="small"
        @finish="modifyLinkRule"
        :labelCol="{ span: 3 }"
        :wrapperCol="{ span: 21 }"
        autocomplete="off"
        :class="`container-form-${ isMobile() ? 'mobile' : 'pc' }`">
        <a-form-item
          label=""
          name="alias"
          extra=""
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="linkRule.alias"/>
        </a-form-item>
        <a-form-item
          label=""
          name="local"
          extra=",  Docker"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-checkbox v-model:checked="linkRule.local"></a-checkbox>
        </a-form-item>
        <a-form-item
          v-if="!linkRule.local"
          label=""
          name="server"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-select :allowClear="true" size="small" v-model:value="linkRule.server"  >
            <a-select-option v-for="server of serverList" v-model:value="server.id" :key="server.id">{{ server.alias }}</a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          label=""
          name="hardlink"
          extra=", "
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-checkbox v-model:checked="linkRule.hardlink"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=""
          name="targetPath"
          extra=": A##B, # "
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="linkRule.targetPath"/>
        </a-form-item>
        <a-form-item
          label=""
          name="linkFilePath"
          extra=", "
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="linkRule.linkFilePath"/>
        </a-form-item>
        <a-form-item
          label="UMASK"
          name="umask"
          extra=", : 0022, !">
          <a-input size="small" v-model:value="linkRule.umask"/>
        </a-form-item>
        <a-form-item
          label=""
          name="keepSeriesName"
          extra=""
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-checkbox v-model:checked="linkRule.keepSeriesName"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=""
          name="group"
          extra=",  - , "
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-checkbox v-model:checked="linkRule.group"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=""
          name="reservedKeys"
          extra=",  , , , ">
          <a-input size="small" v-model:value="linkRule.reservedKeys"/>
        </a-form-item>
        <a-form-item
          label=""
          name="excludeKeys"
          extra=",  , ,   ">
          <a-input size="small" v-model:value="linkRule.excludeKeys"/>
        </a-form-item>
        <a-form-item
          label=""
          name="minFileSize"
          extra=", , , : 5*1024*1024  5MiB"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="linkRule.minFileSize"/>
        </a-form-item>
        <a-form-item
          :wrapperCol="isMobile() ? { span:24 } : { span: 21, offset: 3 }">
          <a-button type="primary" html-type="submit" style="margin-top: 24px; margin-bottom: 48px;"> | </a-button>
          <a-button style="margin-left: 12px; margin-top: 24px; margin-bottom: 48px;" @click="clearLinkRule()"></a-button>
        </a-form-item>
      </a-form>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    const columns = [
      {
        title: 'ID',
        dataIndex: 'id',
        width: 18,
        fixed: true
      }, {
        title: '',
        dataIndex: 'alias',
        width: 30
      }, {
        title: '',
        dataIndex: 'server',
        width: 30
      }, {
        title: '',
        dataIndex: 'linkMode',
        width: 30
      }, {
        title: '',
        width: 20
      }
    ];
    return {
      columns,
      linkRule: {},
      defaultLinkRule: {
        hardlink: false,
        local: false,
        keepSeriesName: false,
        group: false
      },
      loading: true,
      serverList: [],
      linkRules: []
    };
  },
  methods: {
    isMobile () {
      if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
        return true;
      } else {
        return false;
      }
    },
    async listLinkRule () {
      try {
        const res = await this.$api().linkRule.list();
        this.linkRules = res.data;
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    async listServer () {
      try {
        const res = await this.$api().server.list();
        this.serverList = res.data;
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    async modifyLinkRule () {
      try {
        if (this.linkRule.local) {
          this.linkRule.server = '';
        }
        await this.$api().linkRule.modify({ ...this.linkRule });
        this.$message().success((this.linkRule.id ? '' : '') + ', ...');
        setTimeout(() => this.listLinkRule(), 1000);
        this.clearLinkRule();
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    modifyClick (row) {
      this.linkRule = { ...row };
    },
    async deleteLinkRule (row) {
      if (row.used) {
        this.$message().error(', ');
        return;
      }
      try {
        await this.$api().linkRule.delete(row.id);
        this.$message().success(', ...');
        await this.listLinkRule();
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    clearLinkRule () {
      this.linkRule = { ...this.defaultLinkRule };
    }
  },
  async mounted () {
    this.clearLinkRule();
    await this.listServer();
    await this.listLinkRule();
  }
};
</script>
<style scoped>
.link-rule {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}
</style>
