<template>
  <div style="font-size: 24px; font-weight: bold;"> HOSTS</div>
  <a-divider></a-divider>
  <div class="hosts">
    <div style="text-align: left; ">
      <a-textarea  v-model:value="hosts" type="textarea" :rows="10"></a-textarea>
      <div style="margin-top: 16px;">
        <a-button type="primary" @click="save"></a-button>
      </div>
      <div style="margin-top: 16px;">
        <a-button size="small" type="primary" @click="_export"></a-button>
        <a-button size="small" type="primary" style="margin-left: 12px;" @click="_import"></a-button>
      </div>
      <div style="margin-top: 32px;">
          : <br>
          1.  /etc/hosts <br>
          2.  /etc/hosts <br>
          3.  /etc/hosts  vertex/data/hosts <br>
          4.  vertex/data/hosts  /etc/hosts <br>
          5.  hosts <br>
          &nbsp;&nbsp;&nbsp;: 127.0.0.1 localhost<br>
          6.  <br>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      hosts: ''
    };
  },
  methods: {
    isMobile () {
      if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
        return true;
      } else {
        return false;
      }
    },
    async getHosts () {
      try {
        const res = await this.$api().setting.getHosts();
        this.hosts = res.data;
      } catch (e) {
        await this.$message().error(e.message);
      }
    },
    async save () {
      try {
        await this.$api().setting.save({ hosts: this.hosts });
        await this.$message().success('');
      } catch (e) {
        await this.$message().error(e.message);
      }
    },
    async _export () {
      try {
        await this.$api().setting.export();
        await this.$message().success('');
      } catch (e) {
        await this.$message().error(e.message);
      }
    },
    async _import () {
      try {
        await this.$api().setting.import();
        await this.$message().success('');
        this.getHosts();
      } catch (e) {
        await this.$message().error(e.message);
      }
    }
  },
  async mounted () {
    this.getHosts();
  }
};
</script>
<style scoped>
.hosts {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}
</style>
