<template>
  <div style="font-size: 24px; font-weight: bold;"></div>
  <a-divider></a-divider>
  <div class="notification">
    <a-table
      :style="`font-size: ${isMobile() ? '12px': '14px'};`"
      :columns="columns"
      size="small"
      :data-source="notifications"
      :pagination="false"
      :scroll="{ x: 640 }"
    >
      <template #title>
        <span style="font-size: 16px; font-weight: bold;"></span>
      </template>
      <template #bodyCell="{ column, record }">
        <template v-if="column.dataIndex === 'enable'">
          <a-tag color="success" v-if="record.enable"></a-tag>
          <a-tag color="error" v-if="!record.enable"></a-tag>
        </template>
        <template v-if="column.dataIndex === 'status'">
          <a-tag color="success" v-if="record.status"></a-tag>
          <a-tag color="error" v-if="!record.status"></a-tag>
        </template>
        <template v-if="column.title === ''">
          <span>
            <a @click="modifyClick(record)"></a>
            <a-divider type="vertical" />
            <a-popover title="?" trigger="click" :overlayStyle="{ width: '84px', overflow: 'hidden' }">
              <template #content>
                <a-button type="primary" danger @click="deleteNotification(record)" size="small"></a-button>
              </template>
              <a style="color: red"></a>
            </a-popover>
          </span>
        </template>
      </template>
    </a-table>
    <a-divider></a-divider>
    <div style="font-size: 16px; font-weight: bold; padding-left: 8px;"> | </div>
    <div style="text-align: left; ">
      <a-form
        labelAlign="right"
        :labelWrap="true"
        :model="notification"
        size="small"
        @finish="modifyNotification"
        :labelCol="{ span: 3 }"
        :wrapperCol="{ span: 21 }"
        autocomplete="off"
        :class="`container-form-${ isMobile() ? 'mobile' : 'pc' }`">
        <a-form-item
          label=""
          name="alias"
          extra=""
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="notification.alias"/>
        </a-form-item>
        <a-form-item
          label=""
          name="maxErrorCount"
          extra=", ,  100">
          <a-input size="small" v-model:value="notification.maxErrorCount"/>
        </a-form-item>
        <a-form-item
          label=""
          name="clearCountCron"
          extra="Crontab ,  0,  0 * * * *">
          <a-input size="small" v-model:value="notification.clearCountCron"/>
        </a-form-item>
        <a-form-item
          label=""
          name="type"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-select size="small" v-model:value="notification.type"  >
            <a-select-option value="telegram">Telegram</a-select-option>
            <a-select-option value="wechat">WeChat</a-select-option>
            <a-select-option value="slack">Slack</a-select-option>
            <a-select-option value="ntfy">Ntfy</a-select-option>
            <a-select-option value="webhook">Webhook</a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          v-if="notification.type === 'wechat'"
          label=" ID"
          name="corpid"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="notification.corpid"/>
        </a-form-item>
        <a-form-item
          v-if="notification.type === 'wechat'"
          label="Agent ID"
          name="agentid"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="notification.agentid"/>
        </a-form-item>
        <a-form-item
          v-if="notification.type === 'wechat'"
          label="Secret"
          name="corpsecret"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="notification.corpsecret"/>
        </a-form-item>
        <a-form-item
          v-if="notification.type === 'wechat'"
          label="ProxyKey"
          name="proxyKey">
          <a-input size="small" v-model:value="notification.proxyKey"/>
        </a-form-item>
        <a-form-item
          v-if="notification.type === 'telegram'"
          label=" Token"
          name="telegramBotToken"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="notification.telegramBotToken"/>
        </a-form-item>
        <a-form-item
          v-if="notification.type === 'telegram'"
          label=" ID"
          name="telegramChannel"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="notification.telegramChannel"/>
        </a-form-item>
        <a-form-item
          v-if="notification.type === 'ntfy'"
          label="Ntfy URL"
          name="ntfyUrl"
          extra=": https://ntfy.sh/mytopic"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="notification.ntfyUrl"/>
        </a-form-item>
        <a-form-item
          v-if="notification.type === 'ntfy'"
          label=""
          name="ntfyUsername">
          <a-input size="small" v-model:value="notification.ntfyUsername"/>
        </a-form-item>
        <a-form-item
          v-if="notification.type === 'ntfy'"
          label=""
          name="ntfyPassword">
          <a-input size="small" v-model:value="notification.ntfyPassword"/>
        </a-form-item>
        <a-form-item
          v-if="notification.type === 'ntfy'"
          label="Token"
          name="ntfyToken"
          extra="Token+">
          <a-input size="small" v-model:value="notification.ntfyToken"/>
        </a-form-item>
        <a-form-item
          v-if="notification.type === 'ntfy'"
          label=""
          name="ntfyPriority"
          extra="5, 1, 3">
          <a-input size="small" v-model:value="notification.ntfyPriority"/>
        </a-form-item>
        <a-form-item
          v-if="notification.type === 'slack'"
          label="Webhook"
          name="slackWebhook"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="notification.slackWebhook"/>
        </a-form-item>
        <a-form-item
          v-if="notification.type === 'slack'"
          label="Token"
          name="slackToken"
          extra=""
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="notification.slackToken"/>
        </a-form-item>
        <a-form-item
          v-if="notification.type === 'webhook'"
          label="Url"
          name="webhookurl"
          extra=", , "
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="notification.webhookurl"/>
        </a-form-item>
        <a-form-item
          v-if="notification.type === 'webhook'"
          label="Token"
          name="token"
          extra=" token  x-vertex-token "
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="notification.token"/>
        </a-form-item>
        <a-form-item
          label=""
          name="pushType"
          extra=""
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-checkbox-group style="width: 100%;" v-model:value="notification.pushType">
            <a-row>
              <a-col v-for="type of pushType" :span="8" :key="type.key">
                <a-checkbox  v-model:value="type.key">{{ type.value }}</a-checkbox>
              </a-col>
            </a-row>
          </a-checkbox-group>
        </a-form-item>
        <a-form-item
          :wrapperCol="isMobile() ? { span:24 } : { span: 21, offset: 3 }">
          <a-button type="primary" html-type="submit" style="margin-top: 24px; margin-bottom: 48px;"> | </a-button>
          <a-button style="margin-left: 12px; margin-top: 24px; margin-bottom: 48px;" @click="clearNotification()"></a-button>
        </a-form-item>
      </a-form>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    const columns = [
      {
        title: 'ID',
        dataIndex: 'id',
        width: 18,
        sorter: (a, b) => a.id.localeCompare(b.id),
        fixed: true
      }, {
        title: '',
        dataIndex: 'alias',
        sorter: (a, b) => a.alias.localeCompare(b.alias),
        defaultSortOrder: 'ascend',
        width: 30
      }, {
        title: '',
        dataIndex: 'type',
        width: 30
      }, {
        title: '',
        width: 20
      }
    ];
    return {
      columns,
      notifications: [],
      notification: [],
      pushType: [
        {
          key: 'rssError',
          value: 'Rss '
        }, {
          key: 'scrapeError',
          value: ' HR '
        }, {
          key: 'addTorrent',
          value: ''
        }, {
          key: 'addTorrentError',
          value: ''
        }, {
          key: 'rejectTorrent',
          value: ''
        }, {
          key: 'deleteTorrent',
          value: ''
        }, {
          key: 'deleteTorrentError',
          value: ''
        }, {
          key: 'reannounceTorrent',
          value: ''
        }, {
          key: 'reannounceTorrentError',
          value: ''
        }, {
          key: 'connectClient',
          value: ''
        }, {
          key: 'clientLoginError',
          value: ''
        }, {
          key: 'getMaindataError',
          value: ''
        }, {
          key: 'spaceAlarm',
          value: ''
        }, {
          key: 'plexWebhook',
          value: 'Plex '
        }, {
          key: 'embyWebhook',
          value: 'Emby '
        }, {
          key: 'jellyfinWebhook',
          value: 'Jellyfin '
        }, {
          key: 'selectWish',
          value: ' ()'
        }, {
          key: 'addDoubanTorrent',
          value: ''
        }, {
          key: 'addDoubanTorrentError',
          value: ''
        }, {
          key: 'torrentFinish',
          value: ''
        }, {
          key: 'selectTorrentError',
          value: ''
        }, {
          key: 'addDouban',
          value: ''
        }, {
          key: 'startRefreshWish',
          value: ''
        }, {
          key: 'startRefreshWishError',
          value: ''
        }, {
          key: 'addDoubanWish',
          value: ''
        }, {
          key: 'scrapeTorrent',
          value: ''
        }, {
          key: 'scrapeTorrentFailed',
          value: ''
        }, {
          key: 'finish',
          value: ''
        }, {
          key: 'watch',
          value: ''
        }
      ],
      defaultNotification: {
        id: '',
        pushType: []
      },
      loading: true,
      registCode: []
    };
  },
  methods: {
    isMobile () {
      if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
        return true;
      } else {
        return false;
      }
    },
    async listNotification () {
      try {
        const res = await this.$api().notification.list();
        this.notifications = res.data;
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    async modifyNotification () {
      try {
        await this.$api().notification.modify({ ...this.notification });
        this.$message().success((this.notification.id ? '' : '') + ', ...');
        setTimeout(() => this.listNotification(), 1000);
        this.clearNotification();
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    modifyClick (row) {
      this.notification = { ...row, pushType: this.pushType.map(item => item.key).filter(item => row.pushType.indexOf(item) !== -1) };
    },
    clearNotification () {
      this.notification = { ...this.defaultNotification, pushType: [] };
    },
    async deleteNotification (row) {
      if (row.used) {
        this.$message().error(', ');
        return;
      }
      try {
        await this.$api().notification.delete(row.id);
        this.$message().success(', ...');
        await this.listNotification();
      } catch (e) {
        this.$message().error(e.message);
      }
    }
  },
  async mounted () {
    this.clearNotification();
    await this.listNotification();
  }
};
</script>
<style scoped>
.notification {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}
</style>
