<template>
  <div style="font-size: 24px; font-weight: bold;"></div>
  <a-divider></a-divider>
  <div class="security-setting" >
    <div style="text-align: left; ">
      <a-form
        labelAlign="right"
        :labelWrap="true"
        :model="setting"
        size="small"
        @finish="modify"
        :labelCol="{ span: 3 }"
        :wrapperCol="{ span: 21 }"
        autocomplete="off"
        :class="`container-form-${ isMobile() ? 'mobile' : 'pc' }`">
        <a-form-item
          label=""
          name="username"
          extra="">
          <a-input size="small" v-model:value="setting.username"/>
        </a-form-item>
        <a-form-item
          label=""
          name="password"
          extra=", ">
          <a-input size="small" v-model:value="setting.password"/>
        </a-form-item>
        <a-form-item
          label=""
          name="otp"
          extra=",  Authy  Goole Authenticator, , , ,  ****** ">
          <a-input size="small" disabled v-model:value="setting.otp"/>
        </a-form-item>
        <a-form-item
          label=""
          name="otpPw"
          :extra="`, , , : ${$moment(setting.time * 1000).format('HH:mm:ss')}`">
          <a-input size="small" v-model:value="setting.otpPw"/>
        </a-form-item>
        <a-form-item
          label=" SSL "
          extra=" SSL ">
          <a-checkbox v-model:checked="setting.trustAllCerts"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=" Vertex Panel"
          extra=" Vertex Panel, , Vertex Panel ">
          <a-checkbox v-model:checked="setting.trustVertexPanel"></a-checkbox>
        </a-form-item>
        <a-form-item
          label="ApiKey"
          name="apiKey"
          extra="ApiKey  Vertex , ">
          <a-input disabled size="small" v-model:value="setting.apiKey"/>
        </a-form-item>
        <a-form-item
          label="TMDB Api"
          name="tmdbApiKey"
          extra="The Movie Database Api Key, ,  Vertex  api.themoviedb.org">
          <a-input size="small" v-model:value="setting.tmdbApiKey"/>
        </a-form-item>
        <a-form-item
          label="PanelKey"
          name="panelKey"
          extra=" Vertex Panel  ApiKey,  Vertex Panel ">
          <a-input size="small" v-model:value="setting.panelKey"/>
        </a-form-item>
        <a-form-item
          :wrapperCol="isMobile() ? { span:24 } : { span: 21, offset: 3 }">
          <a-button type="primary" html-type="submit" style="margin-top: 24px; margin-bottom: 48px;"></a-button>
        </a-form-item>
      </a-form>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      setting: {}
    };
  },
  methods: {
    isMobile () {
      if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
        return true;
      } else {
        return false;
      }
    },
    async get () {
      try {
        const s = (await this.$api().setting.get()).data;
        this.setting = {
          apiKey: s.apiKey,
          username: s.username,
          password: '',
          panelKey: s.panelKey,
          tmdbApiKey: s.tmdbApiKey,
          otp: s.otp ? '******' : (new Array(16)).fill(1).map(() => '234567ABCDEFGHIJKLMNOPQRSTUVWXYZ'[parseInt(Math.random() * 31)]).join(''),
          otpPw: '',
          time: s.time,
          trustAllCerts: s.trustAllCerts,
          trustVertexPanel: s.trustVertexPanel
        };
      } catch (e) {
        await this.$message().error(e.message);
      }
    },
    async modify () {
      try {
        const setting = { ...this.setting };
        if (setting.password) {
          setting.password = this.$md5(setting.password);
        }
        await this.$api().setting.modify(setting);
        await this.$message().success(', .');
        this.get();
      } catch (e) {
        await this.$message().error(e.message);
      }
    }
  },
  async mounted () {
    await this.get();
  }
};
</script>
<style scoped>
.security-setting {
  height: calc(100% - 92px);
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  text-align: center;
}
</style>
