<template>
  <div style="font-size: 24px; font-weight: bold;"></div>
  <a-divider></a-divider>
  <div class="subscribe-detail" >
    <div style="text-align: left; ">
      <a-form
        labelAlign="right"
        :labelWrap="true"
        :model="item"
        size="small"
        @finish="editItem"
        :labelCol="{ span: 3 }"
        :wrapperCol="{ span: 21 }"
        autocomplete="off"
        :class="`container-form-${ isMobile() ? 'mobile' : 'pc' }`">
        <a-form-item
          label="">
          <a-button type="primary" @click="refreshItem()"></a-button>
          <a-button type="primary" danger style="margin-left: 12px;" @click="deleteItem()"></a-button>
          <a-button style="margin-left: 12px;" @click="$goto('/subscribe/list', $router);"></a-button>
        </a-form-item>
        <a-divider></a-divider>
        <a-form-item
          label=""
          name="name"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="item.name"/>
        </a-form-item>
        <a-form-item
          label=""
          name="tag"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="item.tag"/>
        </a-form-item>
        <a-form-item
          label=""
          name="year"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="item.year"/>
        </a-form-item>
        <a-form-item
          label=""
          name="releaseAt"
          extra=" 2022-06-03">
          <a-input size="small" v-model:value="item.releaseAt"/>
        </a-form-item>
        <a-form-item
          label=""
          name="episodes">
          <a-input size="small" v-model:value="item.episodes"/>
        </a-form-item>
        <a-form-item
          label=""
          name="episodeNow">
          <a-input size="small" v-model:value="item.episodeNow"/>
        </a-form-item>
        <a-form-item
          label=""
          name="downloaded"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-checkbox v-model:checked="item.downloaded"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=""
          name="searchKey"
          extra=", ">
          <a-input size="small" v-model:value="item.searchKey"/>
        </a-form-item>
        <a-form-item
          label=""
          name="acceptKeys"
          extra=" , ,   ">
          <a-input size="small" v-model:value="item.acceptKeys"/>
        </a-form-item>
        <a-form-item
          label=""
          name="rejectKeys"
          extra=" , ,   ">
          <a-input size="small" v-model:value="item.rejectKeys"/>
        </a-form-item>
        <a-form-item
          label=""
          name="removeKeyword"
          extra=", , : \d+  60">
          <a-input size="small" v-model:value="item.removeKeyword"/>
        </a-form-item>
        <a-form-item
          label=""
          name="fixedSeason"
          extra=", ,  ,  0">
          <a-input size="small" v-model:value="item.fixedSeason"/>
        </a-form-item>
        <a-form-item
          label=""
          name="episodeOffset"
          extra=",  -2, -1, 1, 2 , , ">
          <a-input size="small" v-model:value="item.episodeOffset"/>
        </a-form-item>
        <a-form-item
          label=""
          name="restrictYear"
          extra=", ">
          <a-checkbox v-model:checked="item.restrictYear"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=""
          name="rejectCompleteTorrent"
          extra=", , ">
          <a-checkbox v-model:checked="item.rejectCompleteTorrent"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=""
          name="ignoreEpisodes"
          extra=", ">
          <a-checkbox v-model:checked="item.ignoreEpisodes"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=""
          name="cancelLink"
          extra=", ">
          <a-checkbox v-model:checked="item.cancelLink"></a-checkbox>
        </a-form-item>
        <a-form-item
          :wrapperCol="isMobile() ? { span:24 } : { span: 21, offset: 3 }">
          <a-button type="primary" html-type="submit" style="margin-top: 24px; margin-bottom: 48px;"></a-button>
        </a-form-item>
      </a-form>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      item: {}
    };
  },
  methods: {
    isMobile () {
      if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
        return true;
      } else {
        return false;
      }
    },
    async getItem () {
      try {
        this.item = (await this.$api().subscribe.getItem(this.$route.params.id, this.$route.params.douban)).data;
        this.item.downloaded = this.item.downloaded || false;
      } catch (e) {
        await this.$message().error(e.message);
      }
    },
    async refreshItem () {
      try {
        await this.$api().subscribe.refreshItem(this.$route.params.id, this.$route.params.douban);
        await this.$message().success('!');
      } catch (e) {
        await this.$message().error(e.message);
      }
    },
    async deleteItem () {
      try {
        await this.$api().subscribe.deleteItem(this.$route.params.id, this.$route.params.douban);
        await this.$message().success('!');
        this.$goto('/subscribe/list', this.$router);
      } catch (e) {
        await this.$message().error(e.message);
      }
    },
    async editItem () {
      try {
        await this.$api().subscribe.editItem(this.item);
        await this.$message().success('!');
        this.$goto('/subscribe/list', this.$router);
      } catch (e) {
        await this.$message().error(e.message);
      }
    }
  },
  async mounted () {
    await this.getItem();
  }
};
</script>
<style scoped>
.subscribe-detail {
  height: calc(100% - 92px);
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  text-align: center;
}

</style>
