<template>
  <div style="font-size: 24px; font-weight: bold;"></div>
  <a-divider></a-divider>
  <div class="base-setting" >
    <div style="text-align: left; ">
      <a-form
        labelAlign="right"
        :labelWrap="true"
        :model="setting"
        size="small"
        @finish="modify"
        :labelCol="{ span: 3 }"
        :wrapperCol="{ span: 21 }"
        autocomplete="off"
        :class="`container-form-${ isMobile() ? 'mobile' : 'pc' }`">
        <a-form-item
          label="User-Agent"
          name="userAgent"
          extra=" User-Agent,  Vertex">
          <a-input size="small" v-model:value="setting.userAgent"/>
        </a-form-item>
        <a-form-item
          label=""
          name="loggerLevel"
          extra=",  INFO, ">
          <a-select size="small" v-model:value="setting.loggerLevel"  >
            <a-select-option value="info">INFO</a-select-option>
            <a-select-option value="debug">DEBUG</a-select-option>
            <a-select-option value="trace">TRACE</a-select-option>
            <a-select-option value="all">ALL</a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          label="Telegram "
          name="telegramProxy"
          extra="Nginx  Caddy  Telegram  api.telegram.org , : http(s)://ip:port">
          <a-input size="small" v-model:value="setting.telegramProxy"/>
        </a-form-item>
        <a-form-item
          label=""
          name="wechatProxy"
          extra="Nginx  Caddy  qyapi.weixin.qq.com , : http(s)://ip:port/">
          <a-input size="small" v-model:value="setting.wechatProxy"/>
        </a-form-item>
        <a-form-item
          label=""
          extra="">
          <a-checkbox v-model:checked="setting.ignoreError"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=""
          extra="">
          <a-checkbox v-model:checked="setting.ignoreDependCheck"></a-checkbox>
        </a-form-item>
        <a-form-item
          :wrapperCol="isMobile() ? { span:24 } : { span: 21, offset: 3 }">
          <a-button type="primary" html-type="submit" style="margin-top: 24px; margin-bottom: 48px;"></a-button>
        </a-form-item>
      </a-form>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      setting: {}
    };
  },
  methods: {
    isMobile () {
      if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
        return true;
      } else {
        return false;
      }
    },
    async get () {
      try {
        const s = (await this.$api().setting.get()).data;
        this.setting = {
          userAgent: s.userAgent,
          loggerLevel: s.loggerLevel,
          telegramProxy: s.telegramProxy,
          wechatProxy: s.wechatProxy,
          ignoreError: s.ignoreError,
          ignoreDependCheck: s.ignoreDependCheck
        };
      } catch (e) {
        await this.$message().error(e.message);
      }
    },
    async modify () {
      try {
        await this.$api().setting.modify(this.setting);
        await this.$message().success(', .');
        this.get();
      } catch (e) {
        await this.$message().error(e.message);
      }
    }
  },
  async mounted () {
    await this.get();
  }
};
</script>
<style scoped>
.base-setting {
  height: calc(100% - 92px);
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  text-align: center;
}
</style>
