<template>
  <div style="font-size: 24px; font-weight: bold;"></div>
  <a-divider></a-divider>
  <div class="subscribe">
    <a-table
      :style="`font-size: ${isMobile() ? '12px': '14px'};`"
      :columns="columns"
      size="small"
      :data-source="subscribes"
      :pagination="false"
      :scroll="{ x: 640 }"
    >
      <template #title>
        <span style="font-size: 16px; font-weight: bold;"></span>
      </template>
      <template #bodyCell="{ column, record }">
        <template v-if="column.title === ''">
          <span>
            <a @click="refreshSubscribe(record)"></a>
            <a-divider type="vertical" />
            <a-dropdown>
              <a class="ant-dropdown-link" @click.prevent>
                
                <fa :icon="['fas', 'chevron-down']"></fa>
              </a>
              <template #overlay>
                <a-menu>
                  <a-menu-item>
                    <a @click="modifyClick(record)"></a>
                  </a-menu-item>
                  <a-menu-item>
                    <a @click="cloneClick(record)"></a>
                  </a-menu-item>
                  <a-menu-item danger>
                    <a-popover title="?" trigger="click" :overlayStyle="{ width: '84px', overflow: 'hidden' }">
                      <template #content>
                        <a-button type="primary" danger @click="deleteSubscribe(record)" size="small"></a-button>
                      </template>
                      <a></a>
                    </a-popover>
                  </a-menu-item>
                </a-menu>
              </template>
            </a-dropdown>
          </span>
        </template>
      </template>
    </a-table>
    <a-divider></a-divider>
    <div style="font-size: 16px; font-weight: bold; padding-left: 8px;"> | </div>
    <div style="text-align: left; ">
      <a-form
        labelAlign="right"
        :labelWrap="true"
        :model="subscribe"
        size="small"
        @finish="modifySubscribe"
        :labelCol="{ span: 3 }"
        :wrapperCol="{ span: 21 }"
        autocomplete="off"
        :class="`container-form-${ isMobile() ? 'mobile' : 'pc' }`">
        <a-form-item
          label=""
          name="alias"
          extra=""
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="subscribe.alias"/>
        </a-form-item>
        <a-form-item
          label=""
          name="enable"
          extra=""
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-checkbox v-model:checked="subscribe.enable"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=""
          name="sites"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-checkbox-group style="width: 100%;" v-model:value="subscribe.sites">
            <a-row>
              <a-col v-for="site of sites" :span="8" :key="site.name">
                <a-checkbox v-model:value="site.name">{{ site.name }}</a-checkbox>
              </a-col>
            </a-row>
          </a-checkbox-group>
        </a-form-item>
        <a-form-item
          label=""
          name="raceRules"
          :rules="[{ required: true, message: '${label}! ' }]"
          extra=", , ,  0 ">
          <a-checkbox-group style="width: 100%;" v-model:value="subscribe.raceRules">
            <a-row>
              <template v-for="selectRule of selectRules" :key="selectRule.id">
                <a-col v-if="selectRule.priority !== '0'" :span="8">
                  <a-checkbox  v-model:value="selectRule.id">{{ selectRule.alias }}</a-checkbox>
                </a-col>
              </template>
            </a-row>
          </a-checkbox-group>
        </a-form-item>
        <a-form-item
          label=""
          name="rejectRules"
          extra=", , ,  0 ">
          <a-checkbox-group style="width: 100%;" v-model:value="subscribe.rejectRules">
            <a-row>
              <template v-for="selectRule of selectRules" :key="selectRule.id">
                <a-col v-if="selectRule.priority === '0'" :span="8">
                  <a-checkbox  v-model:value="selectRule.id">{{ selectRule.alias }}</a-checkbox>
                </a-col>
              </template>
            </a-row>
          </a-checkbox-group>
        </a-form-item>
        <a-form-item
          label=""
          name="linkRule"
          extra=", ">
          <a-select size="small" v-model:value="subscribe.linkRule">
            <a-select-option v-for="linkRule of linkRules" v-model:value="linkRule.id" :key="linkRule.id">{{ linkRule.alias }}</a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          label=""
          name="client"
          extra=", ">
          <a-select size="small" v-model:value="subscribe.client">
            <a-select-option v-for="downloader of downloaders" v-model:value="downloader.id" :key="downloader.id">{{ downloader.alias }}</a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          label=""
          name="categories"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-form-item-rest>
            <a-table
              :style="`font-size: ${isMobile() ? '12px': '14px'};`"
              :columns="categoriesColumns"
              size="small"
              :data-source="subscribe.categories"
              :pagination="false"
              :scroll="{ x: 980 }"
            >
              <template #bodyCell="{ column, record }">
                <template v-if="column.dataIndex === 'doubanTag'">
                  <a-input size="small" v-model:value="record.doubanTag"/>
                </template>
                <template v-if="column.dataIndex === 'type'">
                  <a-select size="small" v-model:value="record.type">
                    <a-select-option value="movie"></a-select-option>
                    <a-select-option value="series"></a-select-option>
                  </a-select>
                </template>
                <template v-if="column.dataIndex === 'category'">
                  <a-input size="small" v-model:value="record.category"/>
                </template>
                <template v-if="column.dataIndex === 'libraryPath'">
                  <a-input size="small" v-model:value="record.libraryPath"/>
                </template>
                <template v-if="column.dataIndex === 'savePath'">
                  <a-input size="small" v-model:value="record.savePath"/>
                </template>
                <template v-if="column.dataIndex === 'autoTMM'">
                  <a-checkbox v-model:checked="record.autoTMM"></a-checkbox>
                </template>
                <template v-if="column.dataIndex === 'autoSearch'">
                  <a-checkbox v-model:checked="record.autoSearch"></a-checkbox>
                </template>
                <template v-if="column.dataIndex === 'rejectKeys'">
                  <a-input size="small" v-model:value="record.rejectKeys"/>
                </template>
                <template v-if="column.dataIndex === 'rules'">
                  <a-button type="primary" @click="expandModal(record)" size="small"></a-button>
                </template>
                <template v-if="column.dataIndex === 'option'">
                  <a style="color: red" @click="subscribe.categories = subscribe.categories.filter(item => item !== record)"></a>
                </template>
              </template>
            </a-table>
            <a-button
              type="primary" @click="subscribe.categories.push({ ...defaultCategory })"
              size="small"
            ></a-button>
          </a-form-item-rest>
        </a-form-item>
        <a-form-item
          label="Cookie"
          name="cookie"
          extra=" Cookie"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="subscribe.cookie"/>
        </a-form-item>
        <a-form-item
          label=""
          name="users"
          extra=" cookie  ID,  'ID1,ID2,ID3', ">
          <a-input size="small" v-model:value="subscribe.users"/>
        </a-form-item>
        <a-form-item
          label=""
          name="cron"
          extra="  ,  4 , "
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="subscribe.cron"/>
        </a-form-item>
        <a-form-item
          label=""
          name="advancedMode"
          extra=", ,  Vertex Panel "
          >
          <a-checkbox v-model:checked="subscribe.advancedMode"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=""
          name="enableWechatLink"
          extra=",  Token  AesKey">
          <a-checkbox v-model:checked="subscribe.enableWechatLink"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=""
          name="push"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-checkbox v-model:checked="subscribe.push"></a-checkbox>
        </a-form-item>
        <a-form-item
          v-if="subscribe.push"
          label=""
          name="notify"
          extra=", , "
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-select size="small" v-model:value="subscribe.notify">
            <a-select-option v-for="notification of notifications" v-model:value="notification.id" :key="notification.id">{{ notification.alias }}</a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          :wrapperCol="isMobile() ? { span:24 } : { span: 21, offset: 3 }">
          <a-button type="primary" html-type="submit" style="margin-top: 24px; margin-bottom: 48px;"> | </a-button>
          <a-button style="margin-left: 12px; margin-top: 24px; margin-bottom: 48px;"  @click="clearSubscribe()"></a-button>
        </a-form-item>
      </a-form>
    </div>
  </div>
  <a-modal
    v-model:visible="modalVisible"
    title=""
    width="1440px"
    :footer="null">
    <div style="text-align: left; ">
      <a-form
        labelAlign="right"
        :labelWrap="true"
        :model="expandCategory"
        size="small"
        :labelCol="{ span: 3 }"
        :wrapperCol="{ span: 21 }"
        autocomplete="off">
        <a-form-item
          :wrapperCol="{ span:24 }">
          <span>
            {{ expandCategory.doubanTag }}
          </span>
        </a-form-item>
        <a-form-item
          label=""
          name="raceRules"
          :rules="[{ required: true, message: '${label}! ' }]"
          extra=", , ,  0 ">
          <a-checkbox-group style="width: 100%;" v-model:value="expandCategory.raceRules">
            <a-row>
              <template v-for="selectRule of selectRules" :key="selectRule.id">
                <a-col v-if="selectRule.priority !== '0'" :span="8">
                  <a-checkbox  v-model:value="selectRule.id">{{ selectRule.alias }}</a-checkbox>
                </a-col>
              </template>
            </a-row>
          </a-checkbox-group>
        </a-form-item>
        <a-form-item
          label=""
          name="rejectRules"
          extra=", , ,  0 ">
          <a-checkbox-group style="width: 100%;" v-model:value="expandCategory.rejectRules">
            <a-row>
              <template v-for="selectRule of selectRules" :key="selectRule.id">
                <a-col v-if="selectRule.priority === '0'" :span="8">
                  <a-checkbox  v-model:value="selectRule.id">{{ selectRule.alias }}</a-checkbox>
                </a-col>
              </template>
            </a-row>
          </a-checkbox-group>
        </a-form-item>
        <a-form-item
          label=""
          name="defaultEpisodes"
          extra=", ">
          <a-input size="small" v-model:value="expandCategory.defaultEpisodes"/>
        </a-form-item>
        <a-form-item
          label=""
          name="defaultrestrictYear"
          extra=", ">
          <a-checkbox v-model:checked="expandCategory.defaultrestrictYear"></a-checkbox>
        </a-form-item>
      </a-form>
    </div>
  </a-modal>
</template>
<script>
export default {
  data () {
    const columns = [
      {
        title: 'ID',
        dataIndex: 'id',
        width: 5,
        fixed: true
      }, {
        title: '',
        dataIndex: 'alias',
        width: 20
      }, {
        title: '',
        width: 5
      }
    ];
    const categoriesColumns = [
      {
        title: '',
        dataIndex: 'doubanTag',
        width: 24,
        fixed: true
      }, {
        title: '',
        dataIndex: 'type',
        width: 20
      }, {
        title: '',
        dataIndex: 'category',
        width: 20
      }, {
        title: '',
        dataIndex: 'libraryPath',
        width: 20
      }, {
        title: '',
        dataIndex: 'savePath',
        width: 20
      }, {
        title: '',
        dataIndex: 'autoTMM',
        width: 20
      }, {
        title: '',
        dataIndex: 'autoSearch',
        width: 26
      }, {
        title: '',
        dataIndex: 'rejectKeys',
        width: 20
      }, {
        title: '',
        dataIndex: 'rules',
        width: 16
      }, {
        title: '',
        dataIndex: 'option',
        width: 12
      }
    ];
    return {
      columns,
      categoriesColumns,
      modalVisible: false,
      subscribes: [],
      notifications: [],
      selectRules: [],
      downloaders: [],
      subscribe: {},
      sites: [],
      linkRules: [],
      expandCategory: {
        raceRules: [],
        rejectRules: []
      },
      defaultSubscribe: {
        cron: '0 */4 * * *',
        sites: [],
        push: true,
        raceRules: [],
        rejectRules: [],
        categories: []
      },
      defaultCategory: {
        doubanTag: '',
        type: '',
        category: '',
        libraryPath: '',
        savePath: '',
        autoTMM: false,
        rejectKeys: '',
        categoryRules: [],
        autoSearch: true
      },
      loading: true,
      registCode: []
    };
  },
  methods: {
    isMobile () {
      if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
        return true;
      } else {
        return false;
      }
    },
    async listSubscribe () {
      try {
        const res = await this.$api().subscribe.list();
        this.subscribes = res.data;
        for (const s of this.subscribes) {
          for (const c of s.categories) {
            if (!c.raceRules) {
              c.raceRules = [];
            }
            if (!c.rejectRules) {
              c.rejectRules = [];
            }
          }
        }
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    async listNotification () {
      try {
        const res = await this.$api().notification.list();
        this.notifications = res.data;
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    async listSelectRule () {
      try {
        const res = await this.$api().selectRule.list();
        this.selectRules = res.data.sort((a, b) => b.priority - a.priority || a.alias.localeCompare(b.alias));
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    async listSite () {
      try {
        const res = await this.$api().site.list();
        this.sites = res.data.siteList;
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    async listLinkRule () {
      try {
        const res = await this.$api().linkRule.list();
        this.linkRules = res.data;
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    async listDownloader () {
      try {
        const res = await this.$api().downloader.list();
        this.downloaders = res.data;
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    async modifySubscribe () {
      try {
        await this.$api().subscribe.modify({ ...this.subscribe });
        this.$message().success((this.subscribe.id ? '' : '') + ', ...');
        setTimeout(() => this.listSubscribe(), 1000);
        this.clearSubscribe();
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    modifyClick (row) {
      this.subscribe = { ...row };
    },
    cloneClick (row) {
      /*
        sites: [],
        raceRules: [],
        rejectRules: [],
        categories: [{ ...this.defaultCategory }]
      */
      this.subscribe = {
        ...row,
        sites: [...row.sites],
        raceRules: [...row.raceRules],
        rejectRules: [...row.rejectRules],
        categories: [...row.categories.map(item => ({
          ...item,
          raceRules: [...item.raceRules],
          rejectRules: [...item.rejectRules]
        }))]
      };
      this.subscribe.id = null;
      this.subscribe.alias = this.subscribe.alias + '-';
    },
    expandModal (row) {
      this.modalVisible = true;
      this.expandCategory = row;
    },
    async deleteSubscribe (row) {
      try {
        await this.$api().subscribe.delete(row.id);
        this.$message().success(', ...');
        await this.listSubscribe();
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    async refreshSubscribe (row) {
      try {
        await this.$api().subscribe.refreshSubscribe(row.id);
        this.$message().success('');
        await this.listSubscribe();
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    clearSubscribe () {
      this.subscribe = {
        ...this.defaultSubscribe,
        sites: [],
        raceRules: [],
        rejectRules: [],
        categories: [{ ...this.defaultCategory }]
      };
    }
  },
  async mounted () {
    this.clearSubscribe();
    this.listSelectRule();
    this.listSite();
    this.listLinkRule();
    this.listDownloader();
    this.listNotification();
    this.listSubscribe();
  }
};
</script>
<style scoped>
.subscribe {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}
</style>
