<template>
  <div style="font-size: 24px; font-weight: bold;"></div>
  <a-divider></a-divider>
  <div class="delete-rule">
    <a-table
      :style="`font-size: ${isMobile() ? '12px': '14px'};`"
      :columns="columns"
      size="small"
      :data-source="deleteRuleList"
      :pagination="false"
      :scroll="{ x: 640 }"
    >
      <template #title>
        <span style="font-size: 16px; font-weight: bold;"></span>
      </template>
      <template #bodyCell="{ column, record }">
        <template v-if="column.dataIndex === 'type'">
          {{ record.type === 'normal' ? '' : 'JavaScript'}}
        </template>
        <template v-if="column.title === ''">
          <span>
            <a @click="modifyClick(record)"></a>
            <a-divider type="vertical" />
            <a-popover title="?" trigger="click" :overlayStyle="{ width: '84px', overflow: 'hidden' }">
              <template #content>
                <a-button type="primary" danger @click="deleteDeleteRule(record)" size="small"></a-button>
              </template>
              <a style="color: red"></a>
            </a-popover>
          </span>
        </template>
      </template>
    </a-table>
    <a-divider></a-divider>
    <div style="font-size: 16px; font-weight: bold; padding-left: 8px;"> | </div>
    <div style="text-align: left; ">
      <a-form
        labelAlign="right"
        :labelWrap="true"
        :model="deleteRule"
        size="small"
        @finish="modifyDeleteRule"
        :labelCol="{ span: 3 }"
        :wrapperCol="{ span: 21 }"
        autocomplete="off"
        :class="`container-form-${ isMobile() ? 'mobile' : 'pc' }`">
        <a-form-item
          label=""
          name="alias"
          extra=""
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="deleteRule.alias"/>
        </a-form-item>
        <a-form-item
          label=""
          name="fitTime"
          extra=", ,  , , ">
          <a-input size="small" v-model:value="deleteRule.fitTime"/>
        </a-form-item>
        <a-form-item
          label=""
          name="priority"
          extra=",  0">
          <a-input size="small" v-model:value="deleteRule.priority"/>
        </a-form-item>
        <a-form-item
          label=""
          name="deleteNum"
          extra=", , ">
          <a-input size="small" v-model:value="deleteRule.deleteNum"/>
        </a-form-item>
        <a-form-item
          label=""
          name="pause"
          extra="">
          <a-checkbox v-model:checked="deleteRule.pause"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=""
          name="onlyDeleteTorrent"
          extra="">
          <a-checkbox v-model:checked="deleteRule.onlyDeleteTorrent"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=""
          name="limitSpeed"
          extra=", ,  Byte/s, ">
          <a-input size="small" v-model:value="deleteRule.limitSpeed"/>
        </a-form-item>
        <a-form-item
          label=""
          name="type"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-select size="small" v-model:value="deleteRule.type"  >
            <a-select-option value="normal"></a-select-option>
            <a-select-option value="javascript">JavaScript</a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          label=""
          v-if="deleteRule.type === 'normal'"
          name="conditions"
          extra=""
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-table
            :style="`font-size: ${isMobile() ? '12px': '14px'};`"
            :columns="conditionColumns"
            size="small"
            :data-source="deleteRule.conditions"
            :pagination="false"
            :scroll="{ x: 540 }"
          >
            <template #bodyCell="{ column, record }">
              <template v-if="column.dataIndex === 'key'">
                <a-select size="small" v-model:value="record.key"  >
                  <a-select-option v-for="conditionKey of conditionKeys" :key="conditionKey.key" :value="conditionKey.key">{{ conditionKey.name }}</a-select-option>
                </a-select>
              </template>
              <template v-if="column.dataIndex === 'compareType'">
                <a-select size="small" v-model:value="record.compareType"  >
                  <a-select-option value="equals"></a-select-option>
                  <a-select-option value="bigger"></a-select-option>
                  <a-select-option value="smaller"></a-select-option>
                  <a-select-option value="contain"></a-select-option>
                  <a-select-option value="includeIn"></a-select-option>
                  <a-select-option value="notContain"></a-select-option>
                  <a-select-option value="notIncludeIn"></a-select-option>
                  <a-select-option value="regExp"></a-select-option>
                  <a-select-option value="notRegExp"></a-select-option>
                </a-select>
              </template>
              <template v-if="column.dataIndex === 'value'">
                <a-input size="small" v-model:value="record.value"/>
              </template>
              <template v-if="column.dataIndex === 'option'">
                <a style="color: red" @click="deleteRule.conditions = deleteRule.conditions.filter(item => item !== record)"></a>
              </template>
            </template>
          </a-table>
          <a-button
            type="primary"
            @click="deleteRule.conditions.push({ ...condition })"
            size="small"
            >
            
          </a-button>
        </a-form-item>
        <a-form-item
          v-if="deleteRule.type === 'javascript'"
          name="code"
          :rules="[{ required: true, message: '${label}! ' }]"
          label="">
          <a-textarea  v-model:value="deleteRule.code" type="textarea" :rows="10"></a-textarea >
        </a-form-item>
        <a-form-item
          :wrapperCol="isMobile() ? { span:24 } : { span: 21, offset: 3 }">
          <a-button type="primary" html-type="submit" style="margin-top: 24px; margin-bottom: 48px;"> | </a-button>
          <a-button style="margin-left: 12px; margin-top: 24px; margin-bottom: 48px;" @click="clearDeleteRule()"></a-button>
        </a-form-item>
      </a-form>
      <a-divider></a-divider>
      <a-descriptions
        title=""
        :column="1"
        >
        <!--
          01. :  /  <br>
          02. :  /  <br>
          03. :  Tracker <br>
          04. : ,  /s<br>
          05. :   / Byte,  * <br>
          06. :  /s / Byte/s<br>
          07. :  qBittorrent , : <br>
              : `uploading`, : `downloading`
              : `stalledDL`, : `stalledUP`
               qBittorrent Wiki, ,  `stalledDL`
          08. :  Tracker  Tracker <br>
          09. :  0 <br>
              :   8\*3600   22\*3600<br>
               8  22 <br>
              0  Vertex <br>
          10. : <br>
          11. : ,  qBittorrent WebUI  <br>
          12. : , ,  <br>
          13.   /    / :  , ,  KEEP, KEEP2, KEEP3 , :
              `KEEP,KEEP2,KEEP3`
          -->
        <a-descriptions-item label="01. "> /  </a-descriptions-item>
        <a-descriptions-item label="02. "> /  </a-descriptions-item>
        <a-descriptions-item label="03. "> /  </a-descriptions-item>
        <a-descriptions-item label="04. "> Tracker </a-descriptions-item>
        <a-descriptions-item label="05. ">,  /s</a-descriptions-item>
        <a-descriptions-item label="06. ">  / Byte,  * </a-descriptions-item>
        <a-descriptions-item label="07. "> /s / Byte/s</a-descriptions-item>
        <a-descriptions-item label="08. ">
           qBittorrent , :
          <br>
          : uploading, : downloading
          <br>
          : stalledDL, : stalledUP
          <br>
          ,  stalledDL
          <br>
          
          <a href="https://github.com/qbittorrent/qBittorrent/wiki/WebUI-API-(qBittorrent-4.1)#get-torrent-list">qBittorrent Wiki</a>
        </a-descriptions-item>
        <a-descriptions-item label="09. "> Tracker  Tracker </a-descriptions-item>
        <a-descriptions-item label="10. ">
           0 , 0  Vertex 
          <br>
          :   8*3600   22*3600,  8  22 
        </a-descriptions-item>
        <a-descriptions-item label="11. "></a-descriptions-item>
        <a-descriptions-item label="12. ">,  qBittorrent WebUI </a-descriptions-item>
        <a-descriptions-item label="13. ">, , </a-descriptions-item>
        <a-descriptions-item label="14.   /    / ">
           , .  KEEP, KEEP2, KEEP3 , : KEEP,KEEP2,KEEP3
        </a-descriptions-item>
      </a-descriptions>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    const columns = [
      {
        title: 'ID',
        dataIndex: 'id',
        width: 18,
        sorter: (a, b) => a.id.localeCompare(b.id),
        fixed: true
      }, {
        title: '',
        dataIndex: 'alias',
        sorter: (a, b) => a.alias.localeCompare(b.alias),
        width: 30
      }, {
        title: '',
        dataIndex: 'fitTime',
        width: 30
      }, {
        title: '',
        dataIndex: 'priority',
        width: 10
      }, {
        title: '',
        dataIndex: 'type',
        width: 15
      }, {
        title: '',
        width: 20
      }
    ];
    const conditionColumns = [
      {
        title: '',
        dataIndex: 'key',
        width: 18
      }, {
        title: '',
        dataIndex: 'compareType',
        width: 18
      }, {
        title: '',
        dataIndex: 'value',
        width: 90
      }, {
        title: '',
        dataIndex: 'option',
        width: 30
      }
    ];
    return {
      columns,
      conditionColumns,
      conditionKeys: [{
        name: '',
        key: 'name'
      }, {
        name: '',
        key: 'progress'
      }, {
        name: '',
        key: 'uploadSpeed'
      }, {
        name: '',
        key: 'downloadSpeed'
      }, {
        name: '',
        key: 'category'
      }, {
        name: '',
        key: 'tags'
      }, {
        name: '',
        key: 'size'
      }, {
        name: '',
        key: 'totalSize'
      }, {
        name: '',
        key: 'state'
      }, {
        name: '',
        key: 'tracker'
      }, {
        name: '',
        key: 'trackerStatus'
      }, {
        name: '',
        key: 'completed'
      }, {
        name: '',
        key: 'downloaded'
      }, {
        name: '',
        key: 'uploaded'
      }, {
        name: '',
        key: 'ratio'
      }, {
        name: '',
        key: 'trueRatio'
      }, {
        name: '',
        key: 'ratio3'
      }, {
        name: '',
        key: 'addedTime'
      }, {
        name: '',
        key: 'completedTime'
      }, {
        name: '',
        key: 'savePath'
      }, {
        name: '',
        key: 'seeder'
      }, {
        name: '',
        key: 'leecher'
      }, {
        name: '',
        key: 'freeSpace'
      }, {
        name: '',
        key: 'leechingCount'
      }, {
        name: '',
        key: 'seedingCount'
      }, {
        name: '',
        key: 'globalUploadSpeed'
      }, {
        name: '',
        key: 'globalDownloadSpeed'
      }, {
        name: '',
        key: 'secondFromZero'
      }],
      condition: {
        key: '',
        compareType: '',
        value: ''
      },
      deleteRule: {},
      defaultDeleteRule: {
        conditions: [],
        alias: '',
        type: '',
        priority: 0,
        code: '(maindata, torrent) => {\n' +
              '  return false;\n' +
              '}'
      },
      loading: true,
      deleteRuleList: []
    };
  },
  methods: {
    isMobile () {
      if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
        return true;
      } else {
        return false;
      }
    },
    async listDeleteRule () {
      try {
        const res = await this.$api().deleteRule.list();
        this.deleteRuleList = res.data;
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    async modifyDeleteRule () {
      try {
        await this.$api().deleteRule.modify({ ...this.deleteRule });
        this.$message().success((this.deleteRule.id ? '' : '') + ', ...');
        setTimeout(() => this.listDeleteRule(), 1000);
        this.clearDeleteRule();
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    modifyClick (row) {
      this.deleteRule = { ...row };
    },
    async deleteDeleteRule (row) {
      if (row.used) {
        this.$message().error(', ');
        return;
      }
      try {
        await this.$api().deleteRule.delete(row.id);
        this.$message().success(', ...');
        await this.listDeleteRule();
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    clearDeleteRule () {
      this.deleteRule = { ...this.defaultDeleteRule, conditions: [{ ...this.condition }] };
    }
  },
  async mounted () {
    this.clearDeleteRule();
    await this.listDeleteRule();
  }
};
</script>
<style scoped>
.delete-rule {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}
</style>
