<template>
  <div style="font-size: 24px; font-weight: bold;"></div>
  <a-divider></a-divider>
  <div class="watch">
    <a-table
      :style="`font-size: ${isMobile() ? '12px': '14px'};`"
      :columns="columns"
      size="small"
      :data-source="watches"
      :pagination="false"
      :scroll="{ x: 640 }"
    >
      <template #title>
        <span style="font-size: 16px; font-weight: bold;"></span>
      </template>
      <template #bodyCell="{ column, record }">
        <template v-if="column.dataIndex === 'enable'">
          <a-tag color="success" v-if="record.enable"></a-tag>
          <a-tag color="error" v-if="!record.enable"></a-tag>
        </template>
        <template v-if="column.dataIndex === 'downloader'">
          {{ (downloaders.filter(item => item.id === record.downloader)[0] || {}).alias }}
        </template>
        <template v-if="column.title === ''">
          <span>
            <a @click="modifyClick(record)"></a>
            <a-divider type="vertical" />
            <a-popover title="?" trigger="click" :overlayStyle="{ width: '84px', overflow: 'hidden' }">
              <template #content>
                <a-button type="primary" danger @click="deleteWatch(record)" size="small"></a-button>
              </template>
              <a style="color: red"></a>
            </a-popover>
          </span>
        </template>
      </template>
    </a-table>
    <a-divider></a-divider>
    <div style="font-size: 16px; font-weight: bold; padding-left: 8px;"> | </div>
    <div style="text-align: left; ">
      <a-form
        labelAlign="right"
        :labelWrap="true"
        :model="watch"
        size="small"
        @finish="modifyWatch"
        :labelCol="{ span: 3 }"
        :wrapperCol="{ span: 21 }"
        autocomplete="off"
        :class="`container-form-${ isMobile() ? 'mobile' : 'pc' }`">
        <a-form-item
          label=""
          name="alias"
          extra=" "
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="watch.alias"/>
        </a-form-item>
        <a-form-item
          label=""
          name="enable"
          extra=" "
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-checkbox v-model:checked="watch.enable"></a-checkbox>
        </a-form-item>
        <a-form-item
          label=""
          name="cron"
          extra=",  * * * * * "
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="watch.cron"/>
        </a-form-item>
        <a-form-item
          label=""
          name="downloader"
          extra=""
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-select size="small" v-model:value="watch.downloader"  >
            <a-select-option v-for="downloader of downloaders" v-model:value="downloader.id" :key="downloader.id">{{ downloader.alias }}</a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          label=""
          name="notify"
          extra=", , ">
          <a-select size="small" v-model:value="watch.notify">
            <a-select-option v-for="notification of notifications" v-model:value="notification.id" :key="notification.id">{{ notification.alias }}</a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          label=""
          name="type"
          extra=", , , ">
          <a-select size="small" v-model:value="watch.type">
            <a-select-option value="movie"></a-select-option>
            <a-select-option value="series"></a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          label=""
          name="category"
          extra=", , "
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-input size="small" v-model:value="watch.category"/>
        </a-form-item>
        <a-form-item
          label=""
          name="libraryPath"
          extra=", : :, : movie:series"
          :rules="[{ validator: async (rule, value) => { if (value.split(':').length === 2) return; throw ' :,  movie:series' } }]">
          <a-input size="small" v-model:value="watch.libraryPath"/>
        </a-form-item>
        <a-form-item
          label=""
          name="linkRule"
          :rules="[{ required: true, message: '${label}! ' }]">
          <a-select size="small" v-model:value="watch.linkRule">
            <a-select-option v-for="linkRule of linkRules" v-model:value="linkRule.id" :key="linkRule.id">{{ linkRule.alias }}</a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          label=""
          name="linkMode"
          :rules="[{ required: true, message: '${label}! ' }]"
          extra="">
          <a-select size="small" v-model:value="watch.linkMode">
            <a-select-option value="normal"></a-select-option>
            <a-select-option value="keepStruct-1"></a-select-option>
            <a-select-option value="keepStruct-2"></a-select-option>
            <a-select-option value="keepStruct-3"></a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          label=""
          name="forceScrape">
          <a-table
            :style="`font-size: ${isMobile() ? '12px': '14px'};`"
            :columns="scrapeColumns"
            size="small"
            :data-source="watch.forceScrape"
            :pagination="false"
            :scroll="{ x: 540 }"
          >
            <template #bodyCell="{ column, record }">
              <template v-if="column.dataIndex === 'keyword'">
                <a-input size="small" v-model:value="record.keyword"/>
              </template>
              <template v-if="column.dataIndex === 'name'">
                <a-input size="small" v-model:value="record.name"/>
              </template>
              <template v-if="column.dataIndex === 'season'">
                <a-input size="small" v-model:value="record.season"/>
              </template>
              <template v-if="column.dataIndex === 'option'">
                <a style="color: red" @click="watch.forceScrape = watch.forceScrape.filter(item => item !== record)"></a>
              </template>
            </template>
          </a-table>
          <a-button
            type="primary"
            @click="watch.forceScrape.push({ ...scrapeMap })"
            size="small">
            
          </a-button>
        </a-form-item>
        <a-form-item
          :wrapperCol="isMobile() ? { span:24 } : { span: 21, offset: 3 }">
          <a-button type="primary" html-type="submit" style="margin-top: 24px; margin-bottom: 48px;"> | </a-button>
          <a-button style="margin-left: 12px; margin-top: 24px; margin-bottom: 48px;"  @click="clearWatch()"></a-button>
        </a-form-item>
      </a-form>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    const columns = [
      {
        title: 'ID',
        dataIndex: 'id',
        width: 18,
        fixed: true
      }, {
        title: '',
        dataIndex: 'alias',
        width: 20
      }, {
        title: '',
        dataIndex: 'enable',
        width: 15
      }, {
        title: '',
        dataIndex: 'downloader',
        width: 24
      }, {
        title: '',
        dataIndex: 'category',
        width: 24
      }, {
        title: '',
        width: 24
      }
    ];
    const scrapeColumns = [
      {
        title: '',
        dataIndex: 'keyword',
        fixed: true,
        width: 24
      }, {
        title: '',
        dataIndex: 'name',
        width: 24
      }, {
        title: '',
        dataIndex: 'season',
        width: 6
      }, {
        title: '',
        dataIndex: 'option',
        width: 6
      }
    ];
    return {
      columns,
      scrapeColumns,
      watches: [],
      downloaders: [],
      linkRules: [],
      notifications: [],
      watch: {},
      scrapeMap: {
        keyword: '',
        name: '',
        season: ''
      },
      defaultWatch: {
        enable: true,
        cron: '* * * * *',
        downlaoder: '',
        category: '',
        libraryPath: '',
        linkRule: '',
        notify: '',
        forceScrape: []
      },
      loading: true
    };
  },
  methods: {
    isMobile () {
      if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
        return true;
      } else {
        return false;
      }
    },
    async listWatch () {
      this.loading = true;
      try {
        const res = await this.$api().watch.list();
        this.watches = res.data;
      } catch (e) {
        this.$message().error(e.message);
      }
      this.loading = false;
    },
    async listLinkRule () {
      try {
        const res = await this.$api().linkRule.list();
        this.linkRules = res.data;
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    async listDownloader () {
      try {
        const res = await this.$api().downloader.list();
        this.downloaders = res.data;
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    async listNotification () {
      try {
        const res = await this.$api().notification.list();
        this.notifications = res.data;
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    async modifyWatch () {
      try {
        await this.$api().watch.modify({ ...this.watch });
        this.$message().success((this.watch.id ? '' : '') + ', ...');
        setTimeout(() => this.listWatch(), 1000);
        this.clearWatch();
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    modifyClick (row) {
      this.watch = {
        ...row,
        forceScrape: [...row.forceScrape]
      };
    },
    async deleteWatch (row) {
      try {
        await this.$api().watch.delete(row.id);
        this.$message().success(', ...');
        await this.listWatch();
      } catch (e) {
        this.$message().error(e.message);
      }
    },
    clearWatch () {
      this.watch = {
        ...this.defaultWatch,
        forceScrape: []
      };
    }
  },
  async mounted () {
    this.clearWatch();
    this.listWatch();
    this.listDownloader();
    this.listNotification();
    this.listLinkRule();
  }
};
</script>
<style scoped>
.watch {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}
</style>
